<template>
  <v-row>
    <v-col>
      <v-data-table
        :headers="headers"
        :items="records"
        :items-per-page="5"
        class="elevation-1"
        :loading="loading"
      >
        <template v-slot:top>
          <v-row>
            <v-col cols="3">
              <h2 class="mx-4">Plantillas</h2>
            </v-col>
            <v-col>
              <v-btn class="ma-2" small outlined color="primary" @click="$router.push('PlantillasTermRef/form')">
                nueva plantilla
                <v-icon right small>add</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </template>
        <template v-slot:item.acciones="{ item }">
          <v-btn icon color="primary" @click="$router.push('PlantillasTermRef/form/'+item.id)">
            <v-icon>edit</v-icon>
          </v-btn>
          <v-btn :loading="loading" icon color="primary" @click="droprecord(item)" class="ml-2">
            <v-icon>delete</v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-col>
  </v-row>
</template>
<script>
import { plantillas } from '@/servicios/PlantillasService.js'
export default {
  data () {
    return {
      loading: false,
      headers: [
        { text: 'Id', value: 'id' },
        { text: 'Nombre Plantilla', value: 'nombre' },
        { text: 'Acciones', value: 'acciones' }
      ],
      records: []
    }
  },
  mounted () {
    this.getplantillas()
  },
  methods: { 
    async getplantillas() {
      this.loading = true
      try {
        const {data} = await plantillas.getPlantillas()
        this.records = data.termref_plantillas
      } catch (error) {
        console.log(error)
      } finally {
        this.loading = false
      }
    },
    async droprecord(item) {
      this.loading = true
      try {
        const {data} = await plantillas.dropPlantilla({ id: item.id })
        if (data.tipo === 'success') {
          this.$toasted.success(data.respuesta, { icon: 'done', duration: 3000 })
          this.getplantillas()
        } else if (data.tipo === 'error') {
          this.$toasted.error(data.respuesta, { icon: 'error', duration: 3000 })
        }
      } catch (error) {
        console.log(error)
      } finally {
        this.loading = false
      }
    },
  }
}
</script>
